body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.title {
	background-color: rgb(0, 102, 255);
	font-size: 2rem;
	font-weight: 700;
	padding: 15px;
	color: #eee;
}

.fas {
	cursor: pointer;
}

.thumbstack {
	-webkit-animation: 'Spinning'3s forwards;
	        animation: 'Spinning'3s forwards;
	margin-right: 10px;
}


@-webkit-keyframes Spinning {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg)
	}
}


@keyframes Spinning {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg)
	}
}
.list-wrapper {
	width: 50%;
	margin: 0 auto;
	border: 1px solid #cacaca;
	margin-top: 30px;
	border-radius: 15px;
}

.control-bar {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	color: #eee;
	border-radius: 15px;
	background-color: black;
	padding: 0 5px;
}

.control-button {
	font-size: 0.8rem;
	width: 100px;
	height: 40px;
	cursor: pointer;
	font-weight: bold;
	border-radius: 10em;
	line-height: 40px;
	color: white;
	margin: 10px;
}

.add {
	background-color: #32CD32;
}

.remove {
	background-color: #ff0000;
}

.clear {
	background-color: #F8B12A;
}

.tasks-list {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	list-style: none;
}



.task-item {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	padding: 30px 15px;
}

.task-item:nth-child(odd) {
	background-color: #e2e2e2;
}

.task-item:nth-child(even) {
	background-color: #f7f7f7;
}


.task-number {
	width: 20%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.task-description {
	word-wrap: break-word;
	width: 80%;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.check-button {
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
	cursor: pointer;
	width: 30px;
	height: 30px;
	margin-right: 30px;
	-webkit-transition: 1s;
	transition: 1s;
}

.check-button:hover {
	-webkit-filter: grayscale(0);
	        filter: grayscale(0);
	-webkit-transform: scale(1.7);
	        transform: scale(1.7)
}

.task-input {
	resize: none;
	width: 100%;
	min-height: 80px;
}

@-webkit-keyframes TaskRemoved {
	100% {
		background-color: red;
	}
}

@keyframes TaskRemoved {
	100% {
		background-color: red;
	}
}

@-webkit-keyframes TaskCompleted {
	100% {
		background-color: #008000;
	}
}

@keyframes TaskCompleted {
	100% {
		background-color: #008000;
	}
}

@media screen and (max-width: 1052px) {
	.control-bar {
		border-radius: 0;
	}
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
}

.container {
  width: 100vw;
  text-align: center;
  margin: 0 auto;
}

@media screen and (max-width: 1052px) {
  .list-wrapper {
    width: 100%;
    margin: 0;
  }
}
